import { CustomModalBox } from './SmallCompElements'
import { FaTimes } from 'react-icons/fa'
import { useState } from 'react'

const ModalBox = ({ modalImage, modalLink, modalLinkDef, showModal, setShowModal, imageMobile }) => {

    const [modalDesktop, setModalDesktop] = useState(true);

    const handleShowModal = () => {
        setShowModal(false)
    }

    return ( 
        <CustomModalBox showModal={showModal}>
            <div className="modal-overlay" onClick={handleShowModal}></div>
            <div className="modal-content">
                <button className="btn close-modal" onClick={handleShowModal}><FaTimes /></button>
                <div className="modal-tabs">
                    <div className="modal-tabs-ruler">
                        <button className={modalDesktop ? 'active-tab primary--button' : 'primary--button'} onClick={()=>setModalDesktop(true)}>Desktop</button>
                        <button className={!modalDesktop ? 'active-tab primary--button' : 'primary--button'} onClick={()=>setModalDesktop(false)}>Mobile</button>
                    </div>

                    <div className="modal-tabs-content">
                    
                        <div className="modal-img-box">
                         <img src={modalDesktop ? modalImage : imageMobile } />
                        </div>
                        
                    </div>
                </div>
            </div>                      
            <a class="work-link" target="blank" href={modalLink}>{!modalLink ? <span className="modal-link-def">{modalLinkDef}</span> : modalLink}</a>
        </CustomModalBox>
     );
}
 
export default ModalBox;