import './Banner.css'
import me from '../../images/me-2.png'
import NameAnim from '../small-components/NameAnim'
import { React } from 'react'

const Banner = () => {

    return ( 
        <section  className="sc-banner">
            <div className="container">
                <div className="col-row">
                    <div className="col-50">
                        <div className="h1-title-box">
                            <h1>
                                <NameAnim heading="I'm&nbsp;Karen." />
                            </h1>
                        </div>
                        My name is Karen Avetisyan and the main area of my expertise is front-end development.<br/>
                        Have a nice time surfing my page.
                        
                    </div>
                    <div className="col-50 img-box-container text-center">
                        <div className="img-box" >
                            <img src={me} alt="" />
                        </div>
                    </div>
                </div>
            </div>
        </section>
     );
}
 
export default Banner;