import styled from 'styled-components'


export const HeaderContainer = styled.div `
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: ${({scrollNav}) =>(scrollNav ? '#fff' : 'none')};
    box-shadow: ${({scrollNav}) =>(scrollNav ? '0px 0px 15px #f2f2f2' : 'none')};
    margin-top: ${({scrollNav}) =>(scrollNav ? '0px' : '25px')};
    transition: 0.2s;

   @media (max-width: 767px) {
        width: calc(100% - 30px);
        margin-left: 15px;
        margin-top: ${({scrollNav}) =>(scrollNav ? '0px' : '20px')};
    }

`

export const MenuList = styled.div `
    transform: translateX(${({isOpen}) => isOpen ? '0' : '-250px'});
    opacity: ${({isOpen}) => isOpen ? '1' : '0'};
    z-index: ${({isOpen}) => isOpen ? '-1' : '999'};
    visibility: ${({isOpen}) => isOpen ? 'visible' : 'hidden'};

    & li {
        transform: translateX(${({isOpen}) => isOpen ? '0' : '100vh'});
        opacity: ${({isOpen}) => isOpen ? '1' : '0'};
    }
`
