import { useState } from "react";
import './Contact.css';
import { useRef } from 'react';
import emailjs from 'emailjs-com';
import NameAnim from '../small-components/NameAnim';

const Contact = () => {
    const [showResText, setShowResText] = useState();
    const [pending, setPending] = useState(false);

    const form = useRef();

    const sendEmail = (e) => {
        e.preventDefault();
        setPending(true)
        emailjs.sendForm('service_wx88du8', 'template_cs1haj9', e.target, 'user_YZGU1mEhH8mNPr1pLYNTY')
        .then((result) => {
            setPending(false)
            if(result) {
                setShowResText(true);
            }
        }, (error) => {
            if(error) {
                setShowResText(false);
            }
        });
        e.target.reset();
    };

    

    return ( 
        <section  className="sc-contact">
            <div className="container">
                <div className="col-row">
                    <div className="col-100">
                        <div className="h1-title-box">
                            <h1>
                                <NameAnim heading="Contact." />
                            </h1>
                        </div>
                    </div>
                    <div className="col-100">
                        <h2>Here you<br/> can <span>contact me</span></h2>
                        <div className="contact-box">
                            <div>
                                <h3>Email</h3>
                                <p><a href="mailto:kareniweb1995@gmail.com">kareniweb1995@gmail.com</a></p>
                            </div>
                            <div>
                                <h3>Phone</h3>
                                <p><a href="tel:+37491255373">+374-91-255-373</a></p>
                            </div>
                        </div>
                    </div>
                    <div className="col-100">
                        <h2>Fill up a <span>form</span></h2>
                        <div className="form-box">
                            <form ref={form} onSubmit={sendEmail}>
                                <div className="field-box">
                                    <input type="text" required placeholder="Name *" name="user_name" />
                                    <input type="text" required placeholder="Email/Phone no. *" name="user_email" />
                                </div>
                                <div className="field-box">
                                     <input style={{width: '100%'}} type="text" required placeholder="Subject" name="subject" />
                                </div>
                                <div className="text-box">
                                    <textarea placeholder="Message *" name="message" required></textarea>
                                </div>
                                {showResText && <p className="pending-style">Your message was successfully sent!</p>}
                                {pending ? <p className="pending-style">Sending...</p> : ''}
                                <input className="primary--button submit" title="SEND" type="submit" value="Send" />
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
     );
}
 
export default Contact;