import { motion } from 'framer-motion'

const NameAnim = ({ heading }) => {

    const textFlip = {
        show: {
            y: 0,
            scaleX: 1
        },
        hide: {
            y: 180,
            scaleX: 0
        }
    }

    let items = heading.split('')
    let duration = 0.1

    

    const renderName = 
        items.map((item, index, arr) => {
        duration += 0.1
        if (arr.length - 1 === index) {
            duration += 0.7
        }
        return (<motion.span key={index} transition={ { duration: duration } } variants={textFlip} initial='hide' animate='show' exit='hide'>{item.split('')}</motion.span>)
        
    })
    
    return ( 
        <>
            { renderName }
        </>
     );
}
 
export default NameAnim;