import styled from 'styled-components'

export const CustomModalBox = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    z-index: ${({showModal}) => showModal ? '9999' : '-10'} ;
    width: 100%;
    height: 100%;
    transition: 0.2s;
    opacity: ${({showModal}) => showModal ? '1' : '0'} ;
    & .modal-content{
       opacity: ${({showModal}) => showModal ? '1' : '0'} ;
       transform: translateY(${({showModal}) => showModal ? '0' : '-100vh'});
    }

` 