import './About.css'
import me from '../../images/me.jpg'
import { skillsObj, langsObj, selfInfo } from './data'
import NameAnim from '../small-components/NameAnim'

const About = () => {

    // RENDERED SKILLS 
    const renderedSkills = 
        skillsObj.map((skill, index) => (
        <div className="single-skill" key={index}>
            {skill.basic && <span className="basic">Basic</span> }
            <img src={skill.img} alt={skill.alt} title={skill.alt} />
        </div>
        ))

    // RENDERED LANGUAGES 
    const renderedLangs = 
        langsObj.map((elem,index) => (
            <p key={index}>{elem.lang} - {elem.level}</p>
        ))

    return ( 
        <section  className="sc-about">
            <div className="container">
                <div className="col-row">
                    <div className="col-100">
                        <div className="h1-title-box">
                            <h1>
                                <NameAnim heading="About." />
                            </h1>
                        </div>
                    </div>
                    <div className="col-100">
                        <div className="self-info">
                            <div className="title">
                                <h2><span>Front-end</span> developer</h2>
                            </div>
                            <div className="col-row">
                                <div className="info-box">
                                    <div className="col-33 img-box-container">
                                        <div className="img-box"><img src={me} alt="" /></div>
                                    </div>
                                    <div className="col-33">
                                        <div className="text-box">
                                            <h3>{ selfInfo.name }</h3>
                                            <p>{ selfInfo.shortAbout }</p>
                                            <div className="other-info">
                                                <h4>Education</h4>
                                                <p>{ selfInfo.education }</p>
                                            </div>
                                            <div className="other-info">
                                                <h4>Languages</h4>
                                                { renderedLangs }
                                            </div>
                                        </div>
                                        
                                    </div>
                                    <div className="col-33">
                                        <div className="text-box">
                                            <h3>Skills</h3>
                                            <div class="skill-stats">
                                                { renderedSkills }
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
     );
}
 
export default About;