import {  worksObj, fitlerButtons } from '../data'
import { Button } from '../WorkElements'
import { useState } from 'react'
import ModalBox from '../../small-components/CustomModal';
import maskImg from '../../../images/works/mask-image.jpg';

const WorksTabel = ({ sliced }) => {

    const [activeClass, setActiveClass] = useState();
    const [items, setItems] = useState(worksObj);
    const [showModal, setShowModal] = useState(false)
    const [modalImage, setModalImage] = useState()
    const [modalLink, setModalLink] = useState()
    const [modalLinkDef, setModalLinkDef] = useState()
    const [imageMobile, setImageMobile] = useState();

    // CHANGING FILTER BUTTON CLASS 
    const handleClass = (index) => {
        setActiveClass(index);
    }
    
    // FILTERING WORK ITEMS 
    const handleFilter =  (filItem) => {
        const updatedItems = worksObj.filter((elem) => {
            return elem.category.includes(filItem);
        })

        setItems(updatedItems);
    }

    // PASSING DATAS TO MODAL 
    const handleShowModal =  (wImage, wLink, wLinkDef, wImageMobile) => {
        setShowModal(true);
        setModalImage(wImage);
        setModalLink(wLink);
        setModalLinkDef(wLinkDef);
        setImageMobile(wImageMobile)
    }

    // RENDERED WORKS 
    const renderedWorks = 
        sliced ?
        items.slice(0,8).map((work, index) => (
                <div className="single-work" key={index} onClick={()=>handleShowModal(work.image, work.link, work.linkDef, work.imageMobile)}>
                    <div className="work-image" style={{  backgroundImage:`url(${work.image})` }}>
                        <button className="btn work-view"><span>view<br/> project</span></button>
                        <img style={{ opacity: 0 }} src={maskImg} alt="" />
                    </div>
                </div>
        ))
        :
        items.map((work, index) => (
            <div className="single-work" key={index} onClick={()=>handleShowModal(work.image, work.link, work.linkDef, work.imageMobile)}>
                <div className="work-image" style={{  backgroundImage:`url(${work.image})` }}>
                    <button className="btn work-view"><span>view<br/> project</span></button>
                    <img style={{ opacity: 0 }} src={maskImg} alt="" />
                </div>
            </div>
        ))

    // RENDERED FILTER BUTTONS 
    const renderedButtons = 
        
        fitlerButtons.map((elem, index) =>  (
                <li key={index}><Button className={activeClass === elem.buttonIndex ? 'active-btn' : 'btn' } 
                    onClick={()=>{
                        handleClass(elem.buttonIndex); 
                        if (elem.buttonText === 'ALL'){
                            setItems(worksObj)
                        } 
                        else {
                            handleFilter(elem.buttonText); 
                        }
                    } 
                }>{ elem.buttonText }</Button></li>
            )
        )
    
    
    return ( 
        <div className="works-tabel">
            <div className="col-100 text-center">
                <ul className="work-tags">
                    { renderedButtons }
                </ul>
            </div>
            <div className="work-box">
            {renderedWorks}
            <ModalBox modalImage={modalImage} modalLink={modalLink} modalLinkDef={modalLinkDef} imageMobile={imageMobile} setShowModal={setShowModal} showModal={showModal} />
            </div>
        </div>
     );
}
 
export default WorksTabel;