import { NavLink } from "react-router-dom";
import { HeaderContainer, MenuList } from "./HeaderElements";
import { HiOutlineMenuAlt4 } from 'react-icons/hi'
import { VscClose } from 'react-icons/vsc'
import { useState, useEffect } from 'react'
import './Header.css'
import Social from "../small-components/Social";

const Header = () => {

    const [isOpen, setIsOpen] = useState(false);
    const [scrollNav, setScrollNav] = useState(false);

    // NAVBAR STYLE CHANGE ON SCROLL 
    const changeNav = () => {
        if(window.scrollY >= 10) {
            setScrollNav(true)
        }
        else {
            setScrollNav(false)
        }
    }

    // SIDEBAR SHOW/HIDE CLICK FUNCTION 
    const handleClick = () => {
        setIsOpen(!isOpen)
    }

    // SIDEBAR SHOW/HIDE CLICK FUNCTION ONLY FOR FALSE 
    const handleIsOpen = () => {
        setIsOpen(false)
    }

    // USE_EFFECT 
    useEffect(() => {
       window.history.scrollRestoration = 'manual' ;
       window.addEventListener('scroll', changeNav);
    }, []);

    return ( 
        <HeaderContainer className="header" scrollNav={scrollNav} >
            <div className="container">
                <div className="col-row">
                    <div className="navbar">
                        <div className="col-33">
                            <span className="logo-box"><NavLink to="/" onClick={handleIsOpen}>K</NavLink></span>
                        </div>
                        <div className="col-33 text-center">
                            <Social />
                        </div>
                        <div className="col-33 text-right">
                            <div className="menu-box">
                                <button className="btn burger-icon" onClick={handleClick}>
                                    <span className="menu-text">MENU</span>
                                    <HiOutlineMenuAlt4 />
                                </button>
                                <MenuList className="menu-list" isOpen={isOpen}>
                                    <span className="close-menu-list" onClick={handleClick}>
                                        CLOSE <VscClose />
                                    </span>
                                    <li style={{transition: '0.5s ease-in-out'}}><NavLink onClick={handleIsOpen} exact to="/">Home</NavLink></li>
                                    <li style={{transition: '0.7s ease-in-out'}}><NavLink onClick={handleIsOpen} to="/about">About</NavLink></li>
                                    <li style={{transition: '0.9s ease-in-out'}}><NavLink onClick={handleIsOpen} to="/works">Works</NavLink></li>
                                    <li style={{transition: '1.1s ease-in-out'}}><NavLink onClick={handleIsOpen} to="/contact">Contact</NavLink></li>
                                </MenuList> 
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </HeaderContainer> 
     );
}
 
export default Header;

