import './Work.css'
import { useState } from 'react'
import { IoIosArrowRoundForward } from 'react-icons/io'
import PrimaryButton from '../small-components/PrimaryButton'
import WorksTabel from './works-tabel/WorksTabel'
import { Link } from 'react-router-dom'

const Work = () => {
    const [showMore, setShowMore] = useState(true);

    const handleShowMore = () => {
        setShowMore(!showMore)
    }

    return ( 
        <section className="sc-my-works">
            <div className="container">
                <div className="col-row">
                    <div className="col-100">
                        <h2>My <span>works</span></h2>
                    </div>
                </div>
            </div>
            <WorksTabel sliced={true} />
            <div className="container">
                <div className="col-row">
                    <div className="col-100">
                        <div className="text-center">
                            <Link to="/works" className="btn more-works" onClick={handleShowMore}>See More 
                                <span><IoIosArrowRoundForward /></span>
                            </Link>
                        </div>
                    </div>
                    <div className="col-100 text-center">
                        <div className="like-my-works">
                            <h4>Like my work? Want to have a good quality website? <br/>
                            You can contact me now!</h4>
                            <PrimaryButton title="Contact me" link='/contact' />
                        </div>
                    </div>
                </div>
            </div>
        </section>
     );
}
 
export default Work;